@import '~scss/variables';
@import '~scss/custom-properties';

.edit-image {
  display: flex;
  flex-wrap: wrap;

  min-width: 100px;
  min-height: 100px;

  ff-img {
    display: block;
    position: relative;
    margin: .5rem; // prevent scrolling due to cropper handlers
    max-width: 100%;

    img {
      width: auto;
      max-height: 400px;
    }
  }

  .edit-image-toolbar {
    flex: auto;
    align-content: space-between;
    margin: .5rem;

    ff-btn-group {
      margin-top: .5rem;
    }
  }
}
