@import '~scss/variables';
@import '~scss/custom-properties';

ff-color {
  $height: $ff-control-min-height - 0.0666rem;
  $width: $height + $ff-control-padding * 2;

  &, &::before {
    display: block;
    width: $width;
    max-width: $width;
    height: $height;
  }

  &::before {
    content: '';
    width: $height;
    border: 1px solid $ff-control-border-color;
    border-radius: 50%;
    background-color: currentColor;
  }
}

// Color Picker (vanilla-picker)
.layout_default.picker_wrapper.popup {
  width: 280px;
  padding: 1rem;
  border-radius: var(--panel-border-radius);

  &, .picker_arrow::before, .picker_arrow::after {
    background-color: $ff-panel-bg-color;
  }
  .picker_done button {
    background-color: var(--brand-color);
    background-image: none;
    color: #fff;
    text-transform: uppercase;
    border-radius: var(--btn-border-radius);
  }

  .picker_editor input {
    font-family: var(--font-family-minor);
  }
}
